import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
// sessionStore.getItem('user'),
const store = new Vuex.Store({
  state: {
    username: ''
  },
  mutations: {
    setUsername (state, username) {
      state.username = username
    }
  },
  actions: {
    setUsername ({commit}, username) {
      commit('setUsername', username)
    }
  }
})

export default store



